import React from 'react'
import axios from 'axios'

export class LoginForm extends React.Component{
    constructor(props){
        super(props)
        this.state = {name: "", password: ""}
        this.onLoginClick = this.onLoginClick.bind(this)
        this.userInput = this.userInput.bind(this)
    }
    userInput(event, field){
        switch(field){
            case 'login':
                this.setState(state => {
                    return {
                        name: event,
                        password: state.password
                    }
                })
                break;
            case 'pass':
                this.setState(state => {
                    return {
                        name: state.name,
                        password: event
                    }
                })
                break;
        }
    }
    onLoginClick(){
        axios({
            method: 'post',
            url:"/api/session",
            data: JSON.stringify(this.state),
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            }
        })
        .then(data => console.log(data))
        .catch(err => console.log(err))
    }
    render(){
        return(
        <div className="col-8 pad">
          <div className="row mt-4 justify-content-center align-items-center">
            <div className="col-4">
              <input type="text" className="input-group" placeholder="Login" onChange={(event)=>this.userInput(event.target.value, 'login')} />
              <input type="text" className="input-group mt-2" placeholder="Password" onChange={(event)=>this.userInput(event.target.value, 'pass')} />
              <button className="mt-2 btn btn-info" onClick={this.onLoginClick}>LOGIN</button>
            </div>
          </div>
        </div>
        )
    }
}